.footer__title {
  padding: 0;
  margin: 0;
  margin-bottom: 20px;
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 769px) {
  .footer__title {
    padding-top: 20px;
    margin: 0;
  }
}

.footer__navigation {
  display: flex;
}
@media (max-width: 1023px) {
  .footer__navigation {
    flex-direction: column;
  }
}
@media (max-width: 769px) {
  .footer__navigation {
    margin-top: 15px;
  }
}

.footer__inner {
  display: flex;
  width: 100%;
}
@media (max-width: 1023px) {
  .footer__inner {
    width: 100%;
    justify-content: space-between;
    margin: 0 auto;
  }
}
@media (max-width: 769px) {
  .footer__inner {
    margin: 0;
    flex-direction: column;
  }
}

.footer__navigation-list {
  margin: 0;
  margin-right: 30px;
  padding: 0;
  list-style-type: none;
  max-width: 220px;
}
.footer__navigation-list:last-child {
  margin-right: 0;
}

.footer__list-element {
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 10px;
}
.footer__list-element:last-child {
  margin-bottom: 0;
}
.footer__list-element a {
  font-size: 16px;
  line-height: 135%;
  color: rgba(255, 255, 255, 0.7);
}

.footer__list-link:hover {
  color: #fff;
}

@media (max-width: 769px) {
  .footer__contact-wrapper {
    width: 100%;
    position: relative;
  }
  .footer__contact-wrapper::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: -24px;
    border-bottom: 1px solid #fff;
    width: calc(100% + 48px);
  }
}

.footer__contact-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  max-width: 345px;
}
@media (max-width: 769px) {
  .footer__contact-list {
    margin-top: 15px;
  }
}

